@import "../variables";

.pizza-block {
    max-width: calc(25% - 25px);
    width: 100%;
    text-align: center;
    margin-bottom: 65px;
    margin-left: 25px;

    // &:not(:nth-of-type(4n)) {
    //     margin-right: 35px;
    // }

    &__image {
        width: 260px;
    }

    &__title {
        font-size: 20px;
        font-weight: 900;
        letter-spacing: 1%;
        margin-bottom: 20px;
    }

    &__selector {
        display: flex;
        background-color: #f3f3f3;
        border-radius: 10px;
        flex-direction: column;
        padding: 6px;

        ul {
            display: flex;
            flex: 1;

            &:first-of-type {
                margin-bottom: 6px;
            }

            li {
                padding: 8px;
                flex: 1;
                cursor: pointer;
                font-weight: 600;
                font-size: 14px;
                @include noselect();
                &.active {
                    background: #ffffff;
                    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
                    border-radius: 5px;
                    cursor: auto;
                }
                &.disabled {
                    opacity: 0.4;
                    pointer-events: none;
                }
            }
        }
    }

    &__bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 20px;
    }

    &__price {
        font-weight: bold;
        font-size: 22px;
        line-height: 27px;
        letter-spacing: 0.015em;
    }
}

@media screen and (max-width: 1400px) {
    .pizza-block {
        max-width: calc(33.33% - 25px);
        width: 100%;
    }
}

@media screen and (max-width: 1050px) {
    .pizza-block {
        max-width: calc(50% - 25px);
        width: 100%;
    }
}

@media screen and (max-width: 730px) {
    .pizza-block {
        max-width: calc(100%);
        width: 100%;
    }
}
