@import "../variables";

.categories {
    ul {
        display: flex;
        flex-wrap: wrap;

        li {
            background-color: #f9f9f9;
            padding: 13px 30px;
            border-radius: 30px;
            margin-right: 10px;
            font-weight: bold;
            cursor: pointer;
            transition: background-color 0.1s ease-in-out;
            @include noselect();

            &:hover {
                background-color: darken(#f9f9f9, 2%);
            }

            &:active {
                background-color: darken(#f9f9f9, 5%);
            }

            &.active {
                background-color: #282828;
                color: #fff;
            }
        }
    }
}

@media screen and (max-width: 700px) {
    .categories {
        width: 100%;
    }
    .categories ul {
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        text-align: center;
        margin-bottom: 20px;
        width: 100%;
    }
    .categories ul li {
        padding: 13px 30px;
        border-radius: 30px;
        margin-right: 0;
        font-weight: bold;
    }
}
