@font-face {
    font-family: "Proxima Nova";
    src: url("../assets/fonts/ProximaNova-Black.eot");
    src: local("../assets/fonts/Proxima Nova Black"), local("ProximaNova-Black"),
        url("../assets/fonts/ProximaNova-Black.eot?#iefix")
            format("embedded-opentype"),
        url("../assets/fonts/ProximaNova-Black.woff") format("woff"),
        url("../assets/fonts/ProximaNova-Black.ttf") format("truetype");
    font-weight: 900;
    font-style: normal;
}

